@import url(https://fonts.googleapis.com/css?family=Reem+Kufi&display=swap);
@import url(https://fonts.googleapis.com/css?family=Scope+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Reem+Kufi&display=swap);
@import url(https://fonts.googleapis.com/css?family=Scope+One&display=swap);
*,
body,
html {
  margin: 0;
  padding: 0;
  font-family: "Reem Kufi", sans-serif; }

.main {
  width: 100vw; }

h1,
h2,
a {
  letter-spacing: 4px;
  color: #414141;
  font-weight: lighter;
  -webkit-text-decoration-line: none;
          text-decoration-line: none; }

.header-footer {
  font-family: "Reem Kufi", sans-serif;
  letter-spacing: 4px;
  color: dimgray;
  font-weight: lighter; }

.slim-text {
  font-family: "Scope One", serif;
  letter-spacing: 0; }

.cursor {
  cursor: pointer; }

.body {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-items: center; }

.body .animated {
  top: 0;
  left: 0;
  margin-top: 20vh;
  margin-left: 1em; }

@media only screen and (min-width: 800px) {
  .body .animated {
    width: 60vw; } }

.greeting {
  font-size: clamp(3em, 8vw, 10em);
  letter-spacing: 1ex;
  width: 60vw; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-gap: 1em;
  grid-template-rows: repeat(5, -webkit-fit-content);
  grid-template-rows: repeat(5, fit-content);
  text-align: center;
  background-color: black;
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s; }

.item {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  align-self: center; }

.hidden {
  opacity: 0;
  pointer-events: none;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s; }

.toggle {
  color: white;
  position: absolute;
  right: 1em;
  top: 1em;
  z-index: 2;
  padding: 1em;
  cursor: pointer; }

.hover {
  z-index: 2; }

@media only screen and (min-width: 800px) {
  .header {
    position: relative;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: 1em auto 0 auto;
    width: 95vw;
    text-align: center;
    display: grid;
    grid-gap: 2em;
    grid-template-columns: auto repeat(3, -webkit-min-content);
    grid-template-columns: auto repeat(3, min-content);
    text-align: center;
    opacity: 1;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    pointer-events: all; }
    .header :nth-child(1) {
      text-align: left; }
  .toggle {
    opacity: 0;
    pointer-events: none; } }

html {
  background: black; }

h1, h2, a {
  font-family: 'Reem Kufi', sans-serif;
  letter-spacing: 4px;
  color: aliceblue;
  font-weight: lighter;
  -webkit-text-decoration-line: none;
